.mark{cursor:pointer;background-color:#f6ffed;border:1px solid #b7eb8f;border-radius:5px;margin:0;padding:.4em 1em;white-space:pre-line}.mark.selected{border:1px solid red}.mark.highlighted{border:1px dashed #00aeff}.mark.relation{cursor:crosshair}.delete{color:#1890ff;padding-top:.5em;padding-left:1em;line-height:1.8em}.row{display:flex;margin-bottom:.5em}.row div[class~=ant-typography-edit-content]{flex-grow:1;left:-1px;padding:0;margin-top:-1px;margin-bottom:calc(1.6em - 9px)}.row div[class~=ant-typography-edit-content] textarea{padding:.4em 1em}