.section .header{display:flex;justify-content:space-between;padding-left:4px;padding-right:4px;align-items:center}.section .title{flex:1}.treelabels{max-height:auto}.treelabels :global(.ant-tree-switcher):global(.ant-tree-switcher_close) .switcherIcon{transform:rotate(90deg)}.treelabel :global(.ant-tree-node-content-wrapper){cursor:default}.treetag{margin:"5px"}.treetag:hover{opacity:1}.item{color:get-color(error);font-size:14px}.item:hover{cursor:pointer;color:#f5222d}.lstitem{user-select:none;cursor:pointer;align-items:center;justify-content:flex-start;display:flex;flex-wrap:nowrap;position:relative;padding:0;border:1px solid #e5e5e5;border-radius:5px}.lstitem>*:not(:last-child),.lstitem [role=img]{margin-right:6px}.lstitem button.hidden,.lstitem button.visible{height:20px;display:flex;align-items:flex-end;opacity:.8}.lstitem button.hidden span,.lstitem button.visible span{margin-right:0}.lstitem:hover{background-color:var(--labelBgColor)}.lstitem.selected{box-shadow:0 0 0 4px var(--labelBgColor, rgba(0, 0, 0, 0.2))}.lstitem_label{justify-content:space-between}.lstitem__actions{flex:0 0 auto}.lstitem__actions [role=img]{margin:0}.lstitem__actionIcon{flex:0 0 auto;text-align:center;justify-content:center}.list .lstitem{border-bottom:none}.uihidden{opacity:.5}.uivisible{opacity:1}.ant-tree-switcher{margin-top:15px}