.block{display:flex;flex-flow:column;align-items:center;border:1px solid rgba(34,36,38,.15);border-radius:.28571429rem;width:fit-content;padding:.5em}.block:empty{display:none}.divider{margin:12px 0}.button{margin:.3rem 0}.wrapperComponent{flex:1;position:relative;display:flex;align-items:flex-start;justify-content:space-between;align-self:stretch}.wrapper{max-width:100%;width:100%}.loading{z-index:10;position:absolute;top:0;left:0;right:0;bottom:0;width:auto;height:auto;display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;background:rgba(125,125,125,.15);font-size:24px}.image-element{position:absolute}.image_position{position:absolute}.image_position__top{top:0}.image_position__middle{top:50%;transform:translateY(-50%)}.image_position__middle.image_position__center{transform:translate(-50%, -50%)}.image_position__bottom{bottom:0}.image_position__left{left:0}.image_position__center{left:50%;transform:translateX(-50%)}.image_position__right{right:0}.container{overflow:hidden;position:relative;width:100%;height:100%}.container .frame{position:absolute;overflow:hidden;height:0}.container .frame_height{width:100%}.container .filler{max-width:100%;position:relative;overflow:hidden}.container .overlay{position:absolute;top:0;pointer-events:none;z-index:100}.container img{position:absolute;top:0}.withGallery{margin-bottom:80px}.withPagination{padding-top:30px}.gallery{position:absolute;bottom:-80px;display:flex;overflow-x:auto;width:100%;padding-bottom:8px}.gallery img{cursor:pointer;margin-right:4px;border:4px solid transparent;max-width:120px;height:60px;object-fit:cover}.gallery img:hover{border-color:#1890ff66}.gallery img.active{border-color:#1890ff}.pagination{position:absolute;top:0;display:flex;width:100%;padding-right:40px}