.board{--ranker-gap: 8px;--ranker-border-color: rgba(34, 36, 38, 0.15);display:flex;justify-content:space-evenly;width:100%;padding:var(--ranker-gap);border:1px solid var(--ranker-border-color)}.column{flex:1;border:1px solid var(--ranker-border-color);background-color:#fff;padding:var(--ranker-gap);border-radius:2px;display:flex;flex-direction:column;align-items:center;overflow-y:scroll}.column [data-rbd-droppable-id]{flex:1}.column+.column{margin-left:var(--ranker-gap)}.item{display:flex;flex-direction:column;border:1px solid var(--ranker-border-color);margin:var(--ranker-gap) 0 0;padding:var(--ranker-gap);border-radius:2px;background-color:#f5f5f5;color:#000;max-width:400px}.itemLine{margin:0}.dropArea{min-height:50%;min-width:80%;display:flex;flex-direction:column;align-items:stretch}